@use 'sass:math';

// Variables
//
// Variables should follow the `$component-state-property-size` formula for
// consistent naming. Ex: $nav-link-disabled-color and $modal-content-box-shadow-xs.

// Color system

$white:        #fff !default;
$gray-100:     #f8f9fa !default;
$gray-200:     #e9ecef !default;
$gray-300:     #dee2e6 !default;
$gray-400:     #ced4da !default;
$gray-500:     #adb5bd !default;
$gray-600:     #6c757d !default;
$gray-700:     #495057 !default;
$gray-800:     #343a40 !default;
$gray-900:     #212529 !default;
$black:        #000 !default;

$blue:         #007bff !default;
$purple:       #6f42c1 !default;
$pink:         #e83e8c !default;
$red:          #dc3545 !default;
$orange:       #fd7e14 !default;
$yellow:       #ffc107 !default;
$green:        #4caf50 !default;
$teal:         #20c997 !default;
$cyan:         #17a2b8 !default;

$primary:      #5D3B2A !default;
$secondary:    #9D7F63 !default;
$success:        $green !default;
$light-success: rgb(76, 175, 80, 0.25) !default;
$info:         #757575 !default;
$warning:        $yellow !default;
$danger:         $red !default;
$light:          $gray-100 !default;
$dark:           $gray-800 !default;
$gray:           rgb(229,229,229) !default; 
$light-gray:     rgb(229,229,229,0.25) !default; 

$body-bg:        $white !default;

$text-primary:   $primary !default;
$text-secondary: $black !default;
$text-warning:   $red !default;

$thin-font:      100 !default;
$regular-font:   400 !default;
$bold-font:      700 !default;

$buton-text-font:  $bold-font !default;

$border-color:     rgba($black, .25) !default;
$divider-color:    $border-color !default;

// Typography
//
// Font, line-height, and color for body text, headings, and more.

// stylelint-disable value-keyword-case
$font-family-sans-serif:      "Noto Serif Vithkuqi", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, "Noto Sans", sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol", "Noto Color Emoji", "Alex Brush", "Sofia" !default;
$font-family-monospace:       SFMono-Regular, Menlo, Monaco, Consolas, "Liberation Mono", "Courier New", monospace !default;
$font-family-base:            $font-family-sans-serif !default;

$background-color: #F2F2F2